import { InjectionToken } from '@angular/core';
import { environment } from '../environments/environment';


export let APP_CONFIG = new InjectionToken('appConfig');


export let appConfig = {
    endpoint: environment.endpoint,
    user: '/user',
    todos: '/todos',
    todo: '/todo'


};



