import {
    AfterViewInit,
    Component,
    ElementRef,
    EventEmitter,
    OnInit,
    Output,
    Renderer2,
    ViewChild
} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TodoService } from '../todo.service';

@Component({
    selector: 'co-todo-add',
    templateUrl: './todo-add.component.html',
    styleUrls: ['./todo-add.component.scss']
})
export class TodoAddComponent implements OnInit, AfterViewInit {

    isTodoInlineShown: boolean;

    todoForm: FormGroup;

    @ViewChild('titleInput')
    titleInput: ElementRef;


    @ViewChild('toggleBtn')
    toggleBtn: ElementRef;

    isSubmitted: boolean;

    @Output()
    todoAdded: EventEmitter<any> = new EventEmitter();


    constructor(private fb: FormBuilder, private renderer: Renderer2, private todoService: TodoService) {
    }

    ngOnInit() {
        this.isTodoInlineShown = false;
        this.isSubmitted = false;


        this.todoForm = this.fb.group({
            title: ['', Validators.required],
            description: [''],
            status: ['notCompleted', [Validators.required]]
        });
    }

    ngAfterViewInit() {
        // Listen to the click event on toggle button and always set the title input in focus
        this.renderer.listen(this.toggleBtn.nativeElement, 'click', () => {
            this.titleInput.nativeElement.focus();
        });

    }

    /**
     * Toggles the view state of
     * the inline editor
     */
    toggleView() {
        return this.isTodoInlineShown = !this.isTodoInlineShown;
    }

    /**
     * Adds a new 'todo-item'
     * @param value
     */
    addTodo(value) {
        this.isSubmitted = true;
        if (this.todoForm.valid) {
            this.todoService.addTodo(value)
                .subscribe((data) => {
                    switch (data.status) {
                        case 'success':
                            this.toggleView();
                            this.todoForm.reset();
                            this.isSubmitted = false;
                            // Emit event to be caught by the dashboard component
                            this.todoAdded.emit('added')

                            break;
                        case 'error':
                            /*TODO -Add error message*/
                            break;
                        default:
                            break;
                    }
                });
        }

    }

}



