import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import * as CryptoJS from 'crypto-js';
import { AccountService } from '../account.service';
import { IAccount } from '../../shared/interfaces/account';
import { Router } from '@angular/router';
import { fadeInAnimation } from '../../shared/animations/fade.animation';

@Component({
    selector: 'co-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    animations: [fadeInAnimation],
})
export class LoginComponent implements OnInit {

    public loginForm: FormGroup;

    public isSubmitted: boolean;

    public isLoading: boolean;

    errorMessage: string;

    constructor(private fb: FormBuilder, private accountService: AccountService, private router: Router) {
    }

    ngOnInit() {
        this.loginForm = this.fb.group({
            username: ['', [Validators.required]],
            password: ['', [Validators.required]]
        });

        this.isSubmitted = false;
        this.isLoading = false;
    }

    /**
     * Hash password with MD5
     * @param {string} password
     * @returns {string}
     */
    private hashPassword(password: string): string {
        return CryptoJS.MD5(password).toString();
    }


    /**
     * Authenticates a user when
     *  username and password matches.
     *
     *  On successful authentication,
     *  user details is stored and page
     *  is routed to the dashboard
     *
     * @param {FormGroup} form
     */
    login(form: FormGroup) {
        this.isSubmitted = true;

        this.errorMessage = null;

        if (this.loginForm.valid) {
            this.isLoading = true;
            const formData: IAccount = form.value;

            // Replace entered password from form with hashed password
            formData.password = this.hashPassword(formData.password);

            // Call login service
            this.accountService.login(form.value)
                .subscribe((data) => {
                        switch (data.status) {
                            case 'success':
                                setTimeout(() => {
                                    this.accountService.saveUserCredentials(data);
                                    this.router.navigate(['dashboard']);
                                }, 3000);
                                break;

                            case 'error':
                                setTimeout(() => {
                                    this.errorMessage = data.error;
                                    this.isLoading = false;
                                }, 3000);

                                break;
                            default:
                                break;
                        }

                    },
                    (error) => {
                        this.errorMessage = 'Server Error';
                        this.isLoading = false;
                    }
                );
        }

    }


}



