import { Component, OnInit } from '@angular/core';
import { TodoService } from '../todo/todo.service';
import { ITodo } from '../shared/interfaces/todo';


@Component({
    selector: 'co-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

    completedTodos: Array<ITodo>;
    pendingTodos: Array<ITodo>;

    stats;

    alert: object;


    constructor(private todoService: TodoService,) {
    }

    ngOnInit() {
        this.getTodos();
        this.completedTodos = [];
        this.pendingTodos = [];

    }


    /**
     * Get all todos
     */
    getTodos() {
        this.todoService.getTodos()
            .subscribe((data: Array<ITodo>) => {
                this.splitTodos(data);
            });
    }

    /**
     * Splits all Todos into 'Completed'
     * and 'notCompleted'
     * @param {Array<ITodo>} todos
     */

    private splitTodos(todos: Array<ITodo>) {
        for (let i = todos.length - 1; i >= 0; i--) {
            if (todos[i].status === 'completed') {
                this.completedTodos.push(todos[i]);
            }
            if (todos[i].status === 'notCompleted') {
                this.pendingTodos.push(todos[i]);
            }
            todos.pop();
        }

        this.stats = {pending: this.pendingTodos.length, completed: this.completedTodos.length};
    }


    /**
     * THis is called when a new aTodo is added
     * or updated
     */
    handleTodoAdded(data) {
        this.completedTodos = [];
        this.pendingTodos = [];
        this.getTodos();


        //Displays alert
        if (data === 'added') {
            this.alert = {alertMessage: 'New Todo Added', alertType: 'success'};
        }
        if (data === 'updated') {
            this.alert = {alertMessage: 'Todo updated', alertType: 'success'};
        }

        if (data === 'deleted') {
            this.alert = {alertMessage: 'Todo deleted', alertType: 'success'};
        }


        setTimeout(() => {
            this.alert = {alertMessage: '', alertType: ''}
        }, 5000);
    }


}



