import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TodoService } from '../todo.service';

@Component({
    selector: 'co-todo-delete',
    templateUrl: './todo-delete.component.html',
    styleUrls: ['./todo-delete.component.scss']
})
export class TodoDeleteComponent {

    @Input()
    todoId: string;

    @Output()
    todoDeleted: EventEmitter<any> = new EventEmitter();

    constructor(private todoService: TodoService) {
    }

    deleteTodo() {
        this.todoService.deleteTodo({'id': this.todoId})
            .subscribe((data) => {
                switch (data.status) {
                    case 'success':
                        this.todoDeleted.emit('deleted');
                        break;
                    case 'error':
                        break;
                    default:
                        break;
                }
            });
    }

}



