import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { ITodo } from '../shared/interfaces/todo';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TodoService } from './todo.service';

@Component({
    selector: 'co-todo',
    templateUrl: './todo.component.html',
    styleUrls: ['./todo.component.scss']
})
export class TodoComponent implements OnInit {

    @Input()
    todo: ITodo;

    @ViewChild('todoCard')
    todoCard: ElementRef;

    todoForm: FormGroup;

    isSubmitted: boolean;

    showEditor: boolean;

    @Output()
    todoUpdated: EventEmitter<any> = new EventEmitter();


    @Output()
    todoDeleted: EventEmitter<any> = new EventEmitter();


    constructor(private fb: FormBuilder, private todoService: TodoService) {

    }


    ngOnInit() {

        this.isSubmitted = false;

        this.showEditor = false;

        // Instantiate Reactive Form
        this.todoForm = this.fb.group({
            id: ['', Validators.required],
            title: ['', Validators.required],
            description: [''],
            status: ['', [Validators.required]]
        });
    }


    toggleEdit() {
        this.showEditor = true;
        if (this.showEditor) {
            this.prefillForm();
        }
    }

    prefillForm() {
        this.todoForm.setValue({
            id: this.todo._id,
            title: this.todo.title || '',
            description: this.todo.description || '',
            status: this.todo.status || '',
        });
    }

    /**
     * Updates a t\odo item
     * @param value
     */
    updateTodo(value) {
        this.isSubmitted = true;
        if (this.todoForm.valid) {
            this.todoService.updateTodo(value)
                .subscribe((data) => {
                    switch (data.status) {
                        case 'success':
                            this.showEditor = false;
                            this.todoForm.reset();
                            // Emit event to be caught by the dashboard component
                            this.todoUpdated.emit('updated');

                            break;
                        case 'error':
                            /*TODO -Add error message*/
                            break;
                        default:
                            break;
                    }
                });
        }

    }


    /**
     * This is called when
     * a Todo is successfully deleted by
     * the delete component
     */
    handleTodoDeleted() {
        // Event is bubbled up to the Dashboard Component where it is handled
        this.todoDeleted.emit();

    }
}



